import React, { Component } from 'react';
import {HashRouter, Route, Switch} from 'react-router-dom';
import axios from 'axios';
//Components
import Home from './Home';
import SingleCity from './SingleCity';



class Router extends Component {

    apiToken = 'rhzulxn6ub7dvaeaa247hy8986km4y3m';
    url='https://www.triposo.com/api/20181213/';
    autentication = `account=IDGSSQVVIU&token=${this.apiToken}`;
    
    state = { 
        searchCities: [],
        popularCities:[]
     }

     componentDidMount(){
        this.getPopularCities();
    }

    

    getPopularCities = async () => {
        const url = `${this.url}location.json?&part_of=!Egypt&tag_labels=city&count=10&fields=id,name,images,score,country_id&order_by=-score`;
        
        const headers = {
            'X-Triposo-Account': 'GSSQVVIU',
            'X-Triposo-Token': this.apiToken
        }
        await axios.get(url, {headers})
            .then(respuesta => {
                this.setState({
                    popularCities : respuesta.data.results
                })
            })
            .catch(error =>{
                console.log(error);
            })
    }

    searchCity = async (termino) => {
        const url = `${this.url}location.json?tag_labels=city&annotate=trigram:${termino}&trigram=>=0.4&count=3&fields=id,name,score,country_id&order_by=-trigram`;
        
        const headers = {
            'X-Triposo-Account': 'GSSQVVIU',
            'X-Triposo-Token': this.apiToken
        }

        if(termino.length===0){
            this.setState({
                searchCities : []
            })
        }

        await axios.get(url, {headers})
            .then(respuesta => {
                this.setState({
                    searchCities : respuesta.data.results
                })
            })
            .catch(error =>{
                console.log(error);
            })
    }
    
    emptyAutoComplete = () =>{
        this.setState({
            searchCities: []
        })
    }
    


    render() { 
        return ( 
            <HashRouter>
                <React.Fragment>

                <Switch>
                    <Route exact path="/" render={() => (
                                   <Home
                                        cities={this.state.popularCities}
                                        searchCity={this.searchCity}
                                        autoComplete= {this.state.searchCities}
                                   />
                    ) } />

                    <Route exact path="/city/:countryId/:cityId" render={ (props)=>{
                                //elimina el string /city/ y se queda solo con el id
                                //let idCity = props.location.pathname.replace('/city/','');
                                let infoCity = props.location.pathname.split("/");
                                
                                
                                return(
                                    <SingleCity 
                                        id = {infoCity[3]}
                                        country_id = {infoCity[2]}
                                        location = {props.location}
                                        searchCity={this.searchCity}
                                        autoComplete= {this.state.searchCities}
                                        emptyAutoComplete = {this.emptyAutoComplete}

                                    />
                                )
                            }}

                    />
                    <Route render={ () => <h2>404 Error</h2> } />

                </Switch>
                
                </React.Fragment>

                
            
            </HashRouter>
            

         );
    }
}
 
export default Router;