import React, { Component } from 'react';
import {Link} from 'react-router-dom';


class City extends Component {
    liStyle = () => {

        if(this.props.informacion.images[0]){
        return(
            {
                backgroundImage: 'url(' + this.props.informacion.images[0].sizes.medium.url + ')'
            }            
        )
        }else{
            return(
                {
                    background: '#CCC',
                }            
            )

        }
    };

    render() { 
        return (
            <li style= {this.liStyle()}> 
                <Link to={`/city/${this.props.informacion.country_id}/${this.props.informacion.id}`}>
                </Link> 
                <h3>{this.props.informacion.name}</h3>
            </li>
            
         );
    }
}
 
export default City;