import React, { Component } from 'react';

class Weather extends Component {


    convertDate = (date) =>{
        const parts = date.split('-');
        let month;
        switch(parts[1]){
            case "01":
                month='JAN';
                break;
            case "02":
                month='FEB';
                break;
            case "03":
                month='MAR';
                break;
            case "04":
                month='APR';
                break;
            case "05":
                month='MAY';
                break;
            case "06":
                month='JUN';
                break;
            case "07":
                month='JUL';
                break;
            case "08":
                month='AUG';
                break;
            case "09":
                month='SEP';
                break;
            case "10":
                month='OCT';
                break;
            case "11":
                month='NOV';
                break;
            case "12":
                month='DEC';
                break;
            default:
                month='-';
        }

        const result = `${parts[2]} ${month}`;
        return result;

    }
    showResult = () =>{
        if(this.props.weather !== undefined){
            return(
                <div className="weather-container">
                    <ul className="weather">                    
                        {Object.keys(this.props.weather).map(day =>(
                            <li key={day}>                            
                                <p className="date">{this.convertDate(this.props.weather[day].date)}</p>
                                <img src={this.props.weather[day].day.condition.icon} alt="weather-icon"/>
                                <p className="celsius">{this.props.weather[day].day.avgtemp_c.toFixed(0)} â„ƒ</p>
                            </li>
                            
                                        
                        ))}                        
                    </ul>
                </div>
            )
        }
    }


    render() {
        return (
            <section id="weather">
                <h2>WEATHER</h2>
                {this.showResult()}

            </section>
        );
    }
}

export default Weather;