import React, { Component } from 'react';
import City from './City';

class ListCities extends Component {

    render() { 

        return ( 
            <React.Fragment>
                <div className="list-container" id="nearby">
                    <ul className="list-cities" >
                        {Object.keys(this.props.cities).map(city => (
                            <City
                                informacion={this.props.cities[city]}
                                key={city}
                            />
                        )) }
                    </ul>

                </div>
            
            </React.Fragment>
         );
    }
}
 
export default ListCities;