import React, { Component } from 'react';
import {Link} from 'react-router-dom'
class SearchAutoComplete extends Component {

    hide = () =>{
        if(this.props.hideSearch){
            this.props.hideSearch();
        }
    }

    showResult = () => {
        if(this.props.result.length>0){
            return(
            
                <div className="autocomplete">
                    <ul>                    
                        {Object.keys(this.props.result).map(city => (
                            <li key={city}>
                                <Link to={`/city/${this.props.result[city].country_id}/${this.props.result[city].id}`} className="autocomplete-link" onClick={this.hide}>
                                    {this.props.result[city].name}
                                    <span>{this.props.result[city].country_id}</span>
                                 </Link>
                            </li>
                        )) }
                    </ul>
    
                </div>
            )
        }
       

    }


    render() { 
        return ( 
            <React.Fragment>
                {this.showResult()}
            </React.Fragment>

         );
    }
}
 
export default SearchAutoComplete;