import React, { Component } from 'react';

class SeeAndDo extends Component {

    divStyle = (poi) => {

        if(this.props.pointsOfInterest[poi].images[0]){
        return(
            {
                backgroundImage: 'url(' + this.props.pointsOfInterest[poi].images[0].sizes.medium.url + ')',
            }            
        )
        }else{
            return(
                {
                    background: '#CCC',
                }            
            )

        }
    };

     /*
    bestFor = (data) => {
        return(
            Object.keys(this.props.eatAndDrink[data].best_for).map(poi =>(
                <p>{this.props.eatAndDrink[data].best_for[poi].name}</p>
                
            ))
        )
    }
    */

    

    render() { 
        return ( 
            <section id="seeAndDo">
                <h2>PLACES TO SEE</h2>
                <ul className="pointsOfInterest">
                    {Object.keys(this.props.pointsOfInterest).map(poi =>(
                        <li key={poi}>
                            <div style= {this.divStyle(poi)}></div>
                            <h3>{this.props.pointsOfInterest[poi].name}</h3>
                        </li>
                        
                    ))}
                </ul>
            </section>
         );
    }
}
 
export default SeeAndDo;