import React, { Component } from 'react';
import SearchAutoComplete from './SearchAutoComplete';

class Search extends Component {

    terminoRef = React.createRef();


    searchCity = () =>{
        const termino = this.terminoRef.current.value;
        //if(termino.length > 2){
            this.props.searchCity(termino);
        //}
    }
    
   

    render() { 
        return (
            <div className="search-container">
                <input onChange={this.searchCity} ref= {this.terminoRef} type="text" className="search" placeholder='e.g. Barcelona'/>
                <SearchAutoComplete
                    result = {this.props.autoComplete}
                    hideSearch = {this.props.hideSearch}
                />
                
            </div>

         );
    }
}
 
export default Search;