import React, { Component } from 'react';

class Activities extends Component {

     /*
    bestFor = (data) => {
        return(
            Object.keys(this.props.eatAndDrink[data].best_for).map(poi =>(
                <p>{this.props.eatAndDrink[data].best_for[poi].name}</p>
                
            ))
        )
    }
    */

    showResult = () =>{
        if(this.props.activities !== undefined){
            return(
                <ul className="activities">
                    {Object.keys(this.props.activities).map(activity =>(
                        <li key={activity}>
                            <h4>{this.props.activities[activity].name}</h4> 
                            <p>{this.props.activities[activity].snippet}</p> 
                        </li>
                                       
                    ))}
                    
                </ul>
            )
        }
    }

    

    render() { 
        return ( 
            <section id="activities">
                <h2>ACTIVITIES</h2>
                {this.showResult()}
                
            </section>
         );
    }
}
 
export default Activities;