import React, { Component } from 'react';
import axios from 'axios';
import Header from './Header';
import Navegation from './Navegation';
import InfoCity from './InfoCity';
import SeeAndDo from './SeeAnDo';
import MapComponent from './Map';
import ListCities from './ListCities';
import Footer from './Footer';
import Activities from './Activities';
import Weather from './Weather';
import { animateScroll as scroll } from "react-scroll";




class SingleCity extends Component {

    apiToken = 'rhzulxn6ub7dvaeaa247hy8986km4y3m';
    url='https://www.triposo.com/api/20181213/';
    autentication = `account=IDGSSQVVIU&token=${this.apiToken}`;

    state = {
        info:[],
        pointsOfInterest:[],
        activities:[],
        nearbyCities:[],
        weather:[],
        stateChanges:0
    }

    componentDidMount(){
        this.props.emptyAutoComplete();
        this.getData(this.props.id, this.props.country_id);             
    }

    shouldComponentUpdate(nextProps){
        if(this.props.location.pathname !== nextProps.location.pathname ){   
            console.log(this.props.location.pathname);
            console.log(nextProps.location.pathname);
            this.setState({
                info:[],
                pointsOfInterest:[],
                activities:[],
                nearbyCities:[],
                stateChanges:0
            })
            this.props.emptyAutoComplete();
            scroll.scrollToTop(); 

            this.getData(nextProps.id, nextProps.country_id);

            return true;            
        }else{
            return true;
        }
        
        
    }
    

    getData = (id, country_id) =>{
        
        this.getCity(id);
        this.getActivities(id);
        this.getPointsOfInterest(id);
        this.getNearbyCities(id, country_id);
          

    }

    

    getCity = async (id) => {

        const url = `${this.url}location.json?id=${id}&fields=all`;
        
        const headers = {
            'X-Triposo-Account': 'GSSQVVIU',
            'X-Triposo-Token': this.apiToken
        }

        await axios.get(url, {headers})
            .then(respuesta => {
                this.setState({
                    info : respuesta.data.results[0],
                    stateChanges: this.state.stateChanges + 1
                })
                this.getWeather(respuesta.data.results[0].coordinates.latitude, respuesta.data.results[0].coordinates.longitude)
            })
            .catch(error =>{
                console.log(error);
            })


    }


    getPointsOfInterest = async (id) => {

        const url = `${this.url}poi.json?location_id=${id}&fields=images,name,score,snippet&count=12&order_by=-score`;
        
        const headers = {
            'X-Triposo-Account': 'GSSQVVIU',
            'X-Triposo-Token': this.apiToken
        }

        await axios.get(url, {headers})
            .then(respuesta => {
                this.setState({
                    pointsOfInterest : respuesta.data.results,
                    stateChanges: this.state.stateChanges + 1
                })
            })
            .catch(error =>{
                console.log(error);
            })


    }

    getActivities= async (id) => {

        const url = `${this.url}poi.json?location_id=${id}&tag_labels=do&count=10&fields=score,name,snippet&order_by=-score`;
        
        const headers = {
            'X-Triposo-Account': 'GSSQVVIU',
            'X-Triposo-Token': this.apiToken
        }

        await axios.get(url, {headers})
            .then(respuesta => {
                this.setState({
                    activities : respuesta.data.results,
                    stateChanges: this.state.stateChanges + 1
                })
            })
            .catch(error =>{
                console.log(error);
            })
    }

    getNearbyCities = async (cityId, countryId) => {

        const url = `${this.url}location.json?part_of=${countryId}&also_visited=${cityId}&fields=id,name,images,country_id,score&order_by=-also_visited_score`;
        
        const headers = {
            'X-Triposo-Account': 'GSSQVVIU',
            'X-Triposo-Token': this.apiToken
        }

        await axios.get(url, {headers})
            .then(respuesta => {
                this.setState({
                    nearbyCities : respuesta.data.results,
                    stateChanges: this.state.stateChanges + 1
                })
            })
            .catch(error =>{
                console.log(error);
            })


    }

    getWeather = async (latitude, longitude) => {
        const url = `https://api.apixu.com/v1/forecast.json?key=4ca2025811c44d7cb11194042192502&q=${latitude},${longitude}&days=7`;
        const headers = {
            'Accept': '*/*',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
           

        }
        await axios.get(url,{headers})
        .then(respuesta => {
            this.setState({
                weather : respuesta.data.forecast,
                stateChanges: this.state.stateChanges + 1
            })
            console.log(respuesta.data.forecast);
        })
        .catch(error =>{
            console.log(error);
        })


    } 

    

    showResult= () =>{
        if(this.state.stateChanges!==5){
            return(
            <div className="spinner-container">
                <div className="spinner">
                    <div className="double-bounce1"></div>
                    <div className="double-bounce2"></div>
                </div>

            </div>
            )
        }else{
            if (this.state.activities !== undefined && this.state.nearbyCities !== undefined && this.state.pointsOfInterest !== undefined && this.state.info !== undefined){
                return(
                    <React.Fragment>
                        <Header
                            searchCity={this.props.searchCity}
                            autoComplete= {this.props.autoComplete}
                        />
                        <div className="wrapper">
                            <div className="city-container">
    
                                <div className="nav-container">
                                    <Navegation/>
                                </div>
                                
                                <div>                        
                                    <InfoCity
                                        info = {this.state.info}
                                    />
                                    <SeeAndDo
                                        pointsOfInterest= {this.state.pointsOfInterest}
                                    />
                                    <Activities
                                        activities = {this.state.activities}
                                    
                                    />
                                    
                                    <MapComponent
                                        coordinates = {this.state.info.coordinates}
                                    />
                                    <Weather
                                        weather = {this.state.weather.forecastday}
                                    />
                                    <h2>NEARBY CITIES</h2>
                                    <ListCities
                                        cities = {this.state.nearbyCities}
                                    /> 
                                    </div>
                                </div>
                    
                        </div>
                        <Footer/>
                    </React.Fragment>
    
                )
            }

        }

        
    }




    render() { 
        return (
            <React.Fragment>
                {this.showResult()}
            </React.Fragment>
         );
    }
}
 
export default SingleCity;