
import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import Search from './Search';


class Header extends Component {


    showSearch = () =>{
        const container = document.querySelector('.search-mobile-container');
        container.classList.add('flex');
        const searchbtn = document.querySelector('.search-btn');
        searchbtn.classList.add('hide');
        const closebtn = document.querySelector('.close-btn');
        closebtn.classList.add('show');
    }

    hideSearch = () =>{
        const container = document.querySelector('.search-mobile-container');
        container.classList.remove('flex');
        const searchbtn = document.querySelector('.search-btn');
        searchbtn.classList.remove('hide');
        const closebtn = document.querySelector('.close-btn');
        closebtn.classList.remove('show');

    }

    render() {
        return (
            <React.Fragment>            
            <header>
                <div className="wrapper">
                    <div className="header-container">
                        <h1 className="header-logo">
                            <Link to={`/`}>
                                CIT<span>INFO</span>
                            </Link>
                        </h1>
                        <Search
                            searchCity={this.props.searchCity}
                            autoComplete= {this.props.autoComplete}
                        />
                        <div className="search-mobile-trigger" >
                            <svg onClick={this.showSearch} className="search-btn" width="50px" height="50px" viewBox="0 0 50 100" version="1.1" 
  xmlns="...">
                            <path  d="M55.1,51.9L41.6,37.8C45.1,33.6,47,28.4,47,23C47,10.3,36.7,0,24,0S1,10.3,1,23s10.3,23,23,23
	c4.8,0,9.3-1.4,13.2-4.2L50.8,56c0.6,0.6,1.3,0.9,2.2,0.9c0.8,0,1.5-0.3,2.1-0.8C56.3,55,56.3,53.1,55.1,51.9z M24,6
	c9.4,0,17,7.6,17,17s-7.6,17-17,17S7,32.4,7,23S14.6,6,24,6z"/>


                            </svg>
                                                      

                        </div>
                    </div>
                                  
                </div>
                
            </header>

            <div className="search-mobile-container">
                <div className="closebtn-container">
                    <svg onClick={this.hideSearch} className="close-btn" width="50px" height="50px" viewBox="0 0 50 100" version="1.1" 
    xmlns="...">
                        <path d="M28.228,23.986L47.092,5.122c1.172-1.171,1.172-3.071,0-4.242c-1.172-1.172-3.07-1.172-4.242,0L23.986,19.744L5.121,0.88
            c-1.172-1.172-3.07-1.172-4.242,0c-1.172,1.171-1.172,3.071,0,4.242l18.865,18.864L0.879,42.85c-1.172,1.171-1.172,3.071,0,4.242
            C1.465,47.677,2.233,47.97,3,47.97s1.535-0.293,2.121-0.879l18.865-18.864L42.85,47.091c0.586,0.586,1.354,0.879,2.121,0.879
            s1.535-0.293,2.121-0.879c1.172-1.171,1.172-3.071,0-4.242L28.228,23.986z"/>

                    </svg>

                </div>
                
                <div className="search-mobile">
                    <Search
                        searchCity={this.props.searchCity}
                        autoComplete= {this.props.autoComplete}
                        hideSearch = {this.hideSearch}
                    />
                </div>  

            </div>
            </React.Fragment>
                
        );
    }
}

export default Header;